import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';
import { CommonModule, HOOK_NAVIGATOR_NODES, CoreModule } from '@c8y/ngx-components';
import { DeviceListsNavigationFactory } from './device-lists-navigation.factory';
import { DeviceGridExampleGuard } from './device-grid-example-guard';
import { DeviceGridExampleComponent } from './device-grid-example.component';

const routes: Route[] = [
  {
    path: 'grids/device-grid',
    component: DeviceGridExampleComponent,
    canActivate: [DeviceGridExampleGuard]
  }
];

@NgModule({
  imports: [CoreModule, CommonModule, RouterModule.forRoot(routes, { useHash: true })],
  declarations: [DeviceGridExampleComponent],
  entryComponents: [DeviceGridExampleComponent],
  exports: [],
  providers: [
    {
      provide: HOOK_NAVIGATOR_NODES,
      useClass: DeviceListsNavigationFactory,
      multi: true
    },
    DeviceGridExampleGuard
  ]
})
export class DeviceListsModule {}
