import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { DeviceGridExampleGuard } from './device-grid-example-guard';

@Injectable()
export class DeviceListsNavigationFactory implements NavigatorNodeFactory {
  constructor(private deviceGridExampleGuard: DeviceGridExampleGuard) {}

  async get() {
    if (!this.deviceGridExampleGuard.canActivate(null, null)) {
      return [];
    }
    const navs = new NavigatorNode({
      label: gettext('Grids'),
      icon: 'table'
    });
    navs.add(
      new NavigatorNode({
        label: gettext('Device grid'),
        path: '/grids/device-grid',
        icon: 'table'
      })
    );
    return navs;
  }
}
